import { bot } from '../initialize';
import get from 'lodash/fp/get';

const isNewConvo = event =>
  event.type === 'conversationUpdate' && get('membersAdded.length', event);

export default {
  receive(event, next) {
    if (isNewConvo(event)) {
      bot.beginDialog(event.address, '/');
    } else {
      next();
    }
  },
};
